/*********** Navigation Bar ***********/
.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;

}

.links {
  color: black;
  background-color: '#FFD124';
  border-radius: 10px;
  margin-left: auto;
}

.navbar a:hover {
  color: #4F98CA;
}

.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}

.navbar img {
  max-width: 140px;
}


/*********** Form ***********/
.div-forms {
  background-image: url('./assets/Security-Gates-shade.jpg');
  object-fit: cover;
  display: block;
  background-size: cover;
  background-repeat: no-repeat;
  padding-bottom: 100px;
  justify-content:center;
  text-align: center;
  
}

form {    
  padding: 80px 70px;
}

form label {
  font-weight: bold;
  color: #ffffff;
  font-size: large;
}

form button {
  text-transform: uppercase;
  background-color: #8db8e0;
  font-size: large;
  padding: 5px 15px 5px 15px;
  cursor: pointer;
  border: 0 none transparent;
  border-radius: 0;
}

form button:hover {
  text-decoration: none;
  outline-width: 0;
  background-color: #afcde9;
}


/*********** Footer ***********/
footer {
  /* position: relative;
