  padding: 20px;
  /* margin-top: 50px; */
  background-color: #272727;
  /* border-top: 1px solid white; */
  justify-content:center;
  text-align: center;
 
}

footer img {
  max-width: 140px;
  
}

footer p {
  color: #EFFFFB;
  
}